import {LitElement, html, css} from 'lit';
import {classMap} from 'lit/directives/class-map.js';
import {styleMap} from 'lit/directives/style-map.js';


export class Image extends LitElement {
    static styles = css`
        :host {
            display: inline-flex;
            min-height: 1rem;
        }
        
        .image-container {
            width: var(--wrapper-width);
            height: var(--wrapper-height);
            background-color: rgb(var(--secondary-color));
            overflow: hidden;
        }

        .image-container.width-relative {
            width: calc(var(--wrapper-height) * var(--aspect-ratio));
        }
        .image-container.height-relative {
            height: calc(var(--wrapper-width) * var(--aspect-ratio));
        }
        .image-container.circle {
            height: var(--wrapper-width);
            border-radius: calc(var(--wrapper-width)/2);
        }
        .image-container.rounded {
            border-radius: var(--gutter-size);
        }

        .image-container.effect-moon,
        .image-container.hover-effect-moon:hover {
            -webkit-filter: brightness(1.4) contrast(.95) saturate(0) sepia(.35);
            filter: brightness(1.4) contrast(.95) saturate(0) sepia(.35);
        }
        .image-container.effect-1977,
        .image-container.hover-effect-1977:hover {
            -webkit-filter: sepia(.5) hue-rotate(-30deg) saturate(1.4);
            filter: sepia(.5) hue-rotate(-30deg) saturate(1.4);
        }
        
        .image-container.effect-inkwell,
        .image-container.hover-effect-inkwell:hover {
            -webkit-filter: brightness(1.25) contrast(.85) grayscale(1);
            filter: brightness(1.25) contrast(.85) grayscale(1);
        }
        .image-container.effect-sutro,
        .image-container.hover-effect-sutro:hover {
            -webkit-filter: sepia(.4) contrast(1.2) brightness(.9) saturate(1.4) hue-rotate(-10deg);
            filter: sepia(.4) contrast(1.2) brightness(.9) saturate(1.4) hue-rotate(-10deg);
        }
        .image-container.effect-sutro::before,
        .image-container.hover-effect-sutro::before:hover  {
            background: radial-gradient(circle closest-corner,transparent 50%,rgba(0,0,0,.5) 90%);
            background: -o-radial-gradient(circle closest-corner,transparent 50%,rgba(0,0,0,.5) 90%);
            background: -moz-radial-gradient(circle closest-corner,transparent 50%,rgba(0,0,0,.5) 90%);
            background: -webkit-radial-gradient(circle closest-corner,transparent 50%,rgba(0,0,0,.5) 90%);
            content: "";
            mix-blend-mode: darken;
        }
        .image-container.effect-grayscale,
        .image-container.hover-effect-grayscale::hover {
            -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
            filter: grayscale(100%);
        }
        .image-container.effect-blur,
        .image-container.hover-effect-blur:hover {
            -webkit-filter: blur(5px); /* Safari 6.0 - 9.0 */
            filter: blur(5px);
        }
        .image-container.effect-saturate,
        .image-container.hover-effect-saturate:hover {
            filter: saturate(7);
        }
        .image-container.effect-sepia,
        .image-container.hover-effect-sepia:hover {
            filter: sepia(100%);
        }

        img {
            width:100%;
            height: 100%;
            object-fit: var(--image-fit);
        }

    `;

    static properties = {
        source: {
            type: String
        },
        aspect: {
            type: String
        },
        cover: {
            type: Boolean
        },
        contain: {
            type: Boolean
        },
        avatar: {
            type: Boolean
        },
        rounded: {
            type: Boolean
        },
        width: {
            type: String
        },
        height: {
            type: String
        },
        lazy: {
            type: Boolean
        },
        effect: {
            type: String // none, moon, 1977, inkwell, sutro, grayscale, blur, saturate, sepia
        },
        'hover-effect': {
            type: String // none, moon, 1977, inkwell, sutro, grayscale, blur, saturate, sepia
        }
    };

    constructor() { 
        super();
        this.source = null;
        this.aspect = '1';
        this.cover = false;
        this.contain = false;
        this.avatar = false;
        this.rounded = false;
        this.effect = 'none';
        this['hover-effect'] = 'none';
    }

    render() {
        let classes = {
            'image-container': true,
            'lazy': this.lazy,
            'width-relative': !this.width && !!this.height,
            'height-relative': !this.height && !!this.width,
            'circle': this.avatar,
            'rounded': this.rounded

        };

        if (this.effect !== 'none') {
            classes['effect-' + this.effect] = true;
        }
        if (this['hover-effect'] !== 'none') {
            classes['hover-effect-' + this['hover-effect']] = true;
        }
        let styleVars = {
            '--aspect-ratio': this.aspect,
            '--image-fit': this.cover? 'cover' : 'contain',
            '--wrapper-width': this.width,
            '--wrapper-height': this.height
        }

        return html`
            <div class=${classMap(classes)} style=${styleMap(styleVars)}>
                <img .src="${this.source}"></img>
            </div>
        `;
    }
}
customElements.define('zn-image', Image);