import {LitElement, html, css} from 'lit';
import {styleMap} from 'lit/directives/style-map.js';


function hexToRGB(hex) {
  var result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
    let r = parseInt(result[1], 16);
    let g = parseInt(result[2], 16);
    let b = parseInt(result[3], 16);
  return {r, g, b};
}

function RGBToHSL(rgb) {

  let r = rgb.r / 255,
  g = rgb.g / 255,
  b = rgb.b / 255,

  // find greatest and smallest channel values
  cmin = Math.min(r,g,b),
  cmax = Math.max(r,g,b),
  delta = cmax - cmin,
  h = 0,
  s = 0,
  l = 0;

  // calculate hue
  // no difference
  if (delta == 0)
  h = 0;
  // red is max
  else if (cmax == r)
  h = ((g - b) / delta) % 6;
  // green is max
  else if (cmax == g)
  h = (b - r) / delta + 2;
  // blue is max
  else
  h = (r - g) / delta + 4;

  h = Math.round(h * 60);

  // make negative hues positive behind 360Â°
  if (h < 0)
  h += 360;

  // calculate lightness
  l = (cmax + cmin) / 2;

  // calculate saturation
  s = delta == 0 ? 0 : delta / (1 - Math.abs(2 * l - 1));

  // multiply l and s by 100
  s = +(s * 100).toFixed(1);
  l = +(l * 100).toFixed(1);
  return {h, s, l};
}

function HSLToRGB (hsl) {
  let c = Object.assign({}, hsl);
  c.s /= 100;
  c.l /= 100;
  const k = n => (n + c.h / 30) % 12;
  const a = c.s * Math.min(c.l, 1 - c.l);
  const f = n =>
    c.l - a * Math.max(-1, Math.min(k(n) - 3, Math.min(9 - k(n), 1)));
  return { r: 255 * f(0), g: 255 * f(8), b: 255 * f(4)};
};

function hexToHSL (hex) {
  return RGBToHSL(hexToRGB(hex))
}

const defaultPalette = {
  primary: '#0B7925',
  secondary: '#4F684D',
  tertiary: '#006D6A',
  error: '#C20017',
  neutral: '#5C605A',
  neutralVariant: '#596356'
};

const defaultSizes = {
  base: '24px',
  gutter: '0.8rem'
};

function rgbString (rgb) {
  return `${Math.round(rgb.r)}, ${Math.round(rgb.g)}, ${Math.round(rgb.b)}`
}

function hslString (hsl) {
  return `${Math.round(hsl.h)}, ${Math.round(hsl.s)}%, ${Math.round(hsl.l)}%`
}

export const hapticResponse = function (time) {
  if (globalAppConfig.enableHaptics && ('vibrate' in navigator)) navigator.vibrate(time? time : 50);
}

export const updateSystemColor = function (elevated) {
  var meta = document.getElementsByTagName("meta");
  let hasThemeColor = false;
  for (var i = 0; i < meta.length; i++) {
    if (meta[i].name == 'theme-color') {
      meta[i].content = `rgb(${elevated? globalAppConfig.elevatedSystemColor : globalAppConfig.defaultSystemColor})`
      hasThemeColor = true;
    }
  }

  if (!hasThemeColor) {
    let meta = document.createElement('meta');
    meta.name = "theme-color";
    meta.content = `rgb(${elevated? globalAppConfig.elevatedSystemColor : globalAppConfig.defaultSystemColor})`;
    document.head.appendChild(meta);
  }
}

const globalAppConfig = {
  enableHaptics: false,
  elevatedSystemColor: '',
  defaultSystemColor: ''
}

export class ZnApp extends LitElement {
  static styles = css`
  body {
    font-size: var(--base-size);
  }
  :host {
    width: 100vw;
    height: 100vh;
    display: block;
  }
  .zn-app--container {
    width: 100%;
    height: 100%;
  }
`;
  static properties = {
    dark: {
      type: Boolean
    },
    haptics: {
      type: Boolean
    },
    palette: {
      type: Object
    },
    sizes: {
      type: Object
    },
    _colorPalette: {
      state: true
    }
  };

  constructor() {
    super();
    this.dark = false;
    this.haptics = false;
    this.palette = Object.assign({}, defaultPalette);
    this.sizes = Object.assign({}, defaultSizes);
    this.haptics = false;
    this._colorPalette = this.getColorPalette();
    updateSystemColor(false);
  }

  getColorPalette () {
    let working = Object.assign(defaultPalette, this.palette);
    let cssColors = {};
    let darkMode = this.dark;

    Object.keys(working).forEach(function (cKey) {
      let rgb = hexToRGB(working[cKey]);
      cssColors[`--${cKey}-source`] = rgbString(rgb);
      let hsl = RGBToHSL(rgb);
      if (cKey == 'neutral') {
        cssColors[`--background-color`] = rgbString(HSLToRGB(Object.assign(hsl, { l: darkMode? 10 : 99})));
        cssColors[`--on-background-color`] = rgbString(HSLToRGB(Object.assign(hsl, { l: darkMode? 90 : 10})));
        cssColors[`--surface-color`] = rgbString(HSLToRGB(Object.assign(hsl, { l: darkMode? 10 : 99})));
        cssColors[`--on-surface-color`] = rgbString(HSLToRGB(Object.assign(hsl, { l: darkMode? 90 : 10})));
      } else if (cKey == 'neutralVariant') {
        cssColors[`--surface-variant-color`] = rgbString(HSLToRGB(Object.assign(hsl, { l: darkMode? 30 : 90})));
        cssColors[`--on-surface-variant-color`] = rgbString(HSLToRGB(Object.assign(hsl, { l: darkMode? 80 : 30})));
        cssColors[`--outline-color`] = rgbString(HSLToRGB(Object.assign(hsl, { l: darkMode? 60 : 50})));
        cssColors[`--outline-variant-color`] = rgbString(HSLToRGB(Object.assign(hsl, { l: darkMode? 30 : 80})));        
      } else {
        cssColors[`--${cKey}-color`] = rgbString(HSLToRGB(Object.assign(hsl, { l: darkMode? 80 : 40})));
        cssColors[`--on-${cKey}-color`] = rgbString(HSLToRGB(Object.assign(hsl, { l: darkMode? 20 : 100})));
        cssColors[`--${cKey}-container-color`] = rgbString(HSLToRGB(Object.assign(hsl, { l: darkMode? 30 : 90})));
        cssColors[`--on-${cKey}-container-color`] = rgbString(HSLToRGB(Object.assign(hsl, { l: darkMode? 90 : 10})));        
      }
    });

    globalAppConfig.defaultSystemColor = cssColors[`--surface-color`];
    globalAppConfig.elevatedSystemColor = cssColors[`--primary-color`];
    return cssColors;
  } 

  getSizes () {
    let working = Object.assign(defaultSizes, this.sizes);
    let cssSizes = {};

    Object.keys(working).forEach(function (cKey) {
      cssSizes[`--${cKey}-size`] = working[cKey];
    });
    return cssSizes;
  }

  render() {
    let styles = Object.assign({}, this._colorPalette)
    return html`
      <div class="zn-app--container" style=${styleMap(Object.assign(styles, this.getSizes()))}>
        <slot></slot>
      </div>
    `;
  }

  willUpdate(changedProperties) {
    // only need to check changed properties for an expensive computation.
    if (changedProperties.has('haptics')) {
      globalAppConfig.haptics = this.haptics;
    }
    if (changedProperties.has('palette') || changedProperties.has('dark')) {
      this._colorPalette = this.getColorPalette();
    }
  }
}
customElements.define('zn-app', ZnApp);