import {LitElement, html, css} from 'lit';
import '../common/container/Card.js';
import '../common/content/ContentItem.js';
import '../common/button/Button.js';
import '../common/content/Clock.js';

export class ClockCard extends LitElement {
    static styles = css`
        :host {
            display:inline-flex;
            vertical-align:middle;
            user-select:none;
        }
    `;

    static properties = {
        'dark-mode': {
            type: Boolean
        },
        _current: {
            state: true
        }
    };

    constructor() { 
        super();
        this._current = null;
        this['dark-mode'] = false;

    }

    render() {
        return html`
            <zn-card type="filled" full-width="100%"> 
                <zn-content-item>
                    <zn-button slot="right" icon="bedtime" toggle ?active=${this['dark-mode']} rounded filled></zn-button>
                    <zn-clock size="6rem"></zn-clock>
                </zn-content-item>
            </zn-card>
        `;
      }
      _handleTogglePalette (e) {
        let value = e.detail;
        //Intercept current value to disable if is the previous selected
        if (value == this._current) {
            value = null;
        }
        this._current = value;
    }
}
customElements.define('clock-card', ClockCard);