import {LitElement, html, css} from 'lit';
import {ref} from 'lit/directives/ref.js';
import {classMap} from 'lit/directives/class-map.js';

import { updateSystemColor } from './App.js';

import '../button/Button.js';
import '../button/ButtonBar.js';


export class ZnPage extends LitElement {
  static styles = css`
  :host {
    width: 100%;
    height: 100%;
    display: block;
    overflow: hidden;
    background-color: rgb(var(--surface-color));
    color: rgb(var(--on-surface-color));
  }
  .page-bar--container {
    display: flex;
    width: 100%;
    min-height: 3em;
    align-items: center;
    flex-wrap: nowrap;
    transition: background-color .15s linear;
    background-color: rgb(var(--surface-color));
    color: rgb(var(--on-surface-color));
  }

  .page-bar--container.page-bar--container-elevated {
    background-color: rgba(var(--primary-color), 0.08);
  }

  .page-bar--title {
    margin-left: 1em;
    flex-grow: 1;
    user-select: none;
  }
  .page-content--container {
    width: 100%;
    height: calc(100% - 3em);
    max-height: 100%;
    overflow-y: auto;
    overflow-x: hidden;
    scroll-behavior: smooth;
  }

  .page-content--container::-webkit-scrollbar {
    width: 0.5em;
    transition: background-color .15s linear;
  }

  .page-content--container::::-webkit-scrollbar-track {
    background-color: rgba(var(--primary-color), 0.08);
    border-radius: 50%;
  }

  .page-content--container::::-webkit-scrollbar-thumb {
    border-radius: 50%;
    background-color: rgba(var(--primary-color), 0.5);

  }

  .scroll-test {
    width: 100%;
    height: 100vh;
    background-color: salmon;
  }
`;
  static properties = {
    title: {
      type: String
    },
    icon: {
      type: String
    },
    actions: {
      type: Array
    },
    _scrolling: {
      state: true
    },
    _actionToggle: {
      state: true
    }
  };

  constructor() {
    super();
    this.title = 'Page';
    this.icon = null;
    this.actions = [];

    this._scrolling = false;
    this._actionToggle = false;
  }

  render() {
    const barClasses = {'page-bar--container': true, 'page-bar--container-elevated': this._scrolling};
    let mainIcon = html`<zn-button size="lg" icon="${this.icon}" @click="${this._leftActionClick}"></zn-button>`;
    return html`
      <div class=${classMap(barClasses)}>
        ${this.icon? mainIcon : undefined}
        <span class="page-bar--title">${this.title}</span>
        <zn-button-bar disable-labels max-actions="1" .items="${this.actions}" @click="${this._rightActionClick}"></zn-button-bar>
      </div>
      <div class="page-content--container" ${ref(this._attachContentScroll)}>
        <slot></slot>
      </div>
    `;
  }

  _leftActionClick () {
    console.log("Page: left action clicked")
  }
  
  _rightActionClick (e) {
    this.dispatchEvent(new CustomEvent('action', {
      detail: e.detail,
      bubbles: false,
      composed: true
    }));        
  }

  _attachContentScroll (el) {
    el?.addEventListener('scroll', (event) => {
      this._scrolling = event.target.scrollTop > 0;
      updateSystemColor(this._scrolling);
    });
  }
}
customElements.define('zn-page', ZnPage);