const toString = Object.prototype.toString;
export const isString = function(value) {
  return toString.call(value) === '[object String]';
};

export const isNumber = function(value) {
  return Number.isFinite(value);
};

// is a given value object?
export const isObject = function(value) {
  return Object(value) === value;
};

export const isArray = Array.isArray || function(value) {    // check native isArray first
  return toString.call(value) === '[object Array]';
};

export const isFunction = function(value) {    // fallback check is for IE
  return toString.call(value) === '[object Function]' || typeof value === 'function';
};
export const isJson = function(value) {
  return toString.call(value) === '[object Object]';
};
export const isBoolean = function(value) {
  return value === true || value === false || toString.call(value) === '[object Boolean]';
};

export const isEmpty = function(value) {
  if (isObject(value)) {
      var length = Object.getOwnPropertyNames(value).length;
      if (length === 0 || (length === 1 && isArray(value))) {
          return true;
      }
      return false;
  }
  return value === '';
};

export const isEqual = function(value, other) {
  // check 0 and -0 equity with Infinity and -Infinity
  if (isNumber(value) && isNumber(other)) {
      return value === other && 1 / value === 1 / other;
  }
  // check regexes as strings too
  if (isString(value) && isString(other)) {
      return '' + value === '' + other;
  }
  if (isBoolean(value) && isBoolean(other)) {
      return value === other;
  }
  return false;
};

export const isNull = function(value) {
  return value === null;
};

export const isUndefined = function(value) {
  return value === void 0;
};

export const hasValue = function (value) {
  return !isNull(value) && !isUndefined(value);
}


export const defer = function () {
	var res, rej;

	var promise = new Promise((resolve, reject) => {
		res = resolve;
		rej = reject;
	});

	promise.resolve = res;
	promise.reject = rej;

	return promise;
}