import {LitElement, html, css} from 'lit';
import {classMap} from 'lit/directives/class-map.js';
import {styleMap} from 'lit/directives/style-map.js';
import {ref} from 'lit/directives/ref.js';


export class Popover extends LitElement {
    static styles = css`
        :host {
            display:inline-flex;
            position: relative;
            user-select:none;
            height: fit-content;
        }

        .popover-content {
            width: fit-content;
            height: fit-content;

            visibility: hidden;
            opacity: 0;

            position: absolute;
            /* this will be the default placement as center-center */
            bottom: calc((-0.5) * (var(--popover-content-height) - var(--popover-trigger-height)));
            right: calc((-0.5) * (var(--popover-content-width) - var(--popover-trigger-width)));

        }

        .popover-content.open {
            opacity: 1;
            visibility: visible;
        }

        .popover-content.position-bottom {
            bottom: calc((-1) * (var(--popover-content-height) + var(--popover-trigger-height )/2));
        }
        .popover-content.position-top  {
            bottom: var(--popover-trigger-height);
        }
        .popover-content.position-right  {
            right: var(--popover-trigger-width);
        }
        .popover-content.position-left  {
            right: 0;
        }
    `;

    static properties = {
        position: {
            type: String
        },
        open: {
            type: Boolean
        },
        _triggerWidth: {
            state: true
        },
        _triggerHeight: {
            state: true
        },
        _contentWidth: {
            state: true
        },
        _contentHeight: {
            state: true
        }
    };

    constructor() { 
        super();
        this.position = '';
        this.open = false;
        this._triggerWidth = 0;
        this._triggerHeight = 0;
        this._contentWidth = 0;
        this._contentHeight = 0;
    }
      
    render() {
        const contentClass= {
            'popover-content': true,
            open: this.open,
            'position-left': this.position.includes('left'),
            'position-right': this.position.includes('right'),
            'position-top': this.position.includes('top'),
            'position-bottom': this.position.includes('bottom')
        };
        const styleVars= {
            '--popover-trigger-width': `${this._triggerWidth}px`,
            '--popover-trigger-height': `${this._triggerHeight}px`,
            '--popover-content-width': `${this._contentWidth}px`,
            '--popover-content-height': `${this._contentHeight}px`
        }
        return html`
            <div class="popover-trigger"
                ${ref(this._triggerChanged)}>
                <slot></slot>
            </div>
            <div class=${classMap(contentClass)}
                style=${styleMap(styleVars)}
                ${ref(this._contentChanged)}>
                <slot name="content"></slot>
            </div>
        `;
    }

    _triggerChanged (el) {
        if (el) {
            el.addEventListener('slotchange',
                e => {
                    console.log("slotchange: " + e.target)
                    this._triggerWidth = e.currentTarget.clientWidth;
                    this._triggerHeight = e.currentTarget.clientHeight;
                }
            );
        }
    }

    _contentChanged (el) {
        if (el) {
            el.addEventListener('slotchange',
                e => {
                    console.log("slotchange: " + e.target)
                    this._contentWidth = e.currentTarget.clientWidth;
                    this._contentHeight = e.currentTarget.clientHeight;
                }
            );
        }
    }
}

customElements.define('zn-popover', Popover);