import {LitElement, html, css} from 'lit';
import {classMap} from 'lit/directives/class-map.js';
import { hapticResponse } from '../layout/App.js';
import '../content/Icon.js';


export class Button extends LitElement {
    static styles = css`
        :host {
            display:inline-flex;
            vertical-align:middle;
            user-select:none;
            --overall-size: calc(var(--gutter-size) + var(--base-size));

        }

        /* Standard & base Icon Button */
        div {
            display: inline-flex;
            height: var(--overall-size);
            line-height: var(--overall-size);  
            width: var(--overall-size);  
            font-size: var(--base-size);
            border-radius: 50%;
            background-color: transparent;
            color: rgb(var(--on-surface-variant-color));
            align-items: center;
            justify-content: center;
            cursor: pointer;
            transition: all .15s linear;
        }

        /* Labeled base button */
        div.labeled {
            width: auto;
            border-radius: var(--base-size);
            padding: 0.2rem 0.4rem;   
        }
    
        div.size-sm {
            height: calc(var(--overall-size) * 0.8);
            line-height: calc(var(--overall-size) * 0.8);  
            width: calc(var(--overall-size) * 0.8); 
            font-size: calc(var(--base-size) * 0.8);
        }

        .size-lg {
            height: calc(var(--overall-size) * 1.2);
            line-height: calc(var(--overall-size) * 1.2);  
            width: calc(var(--overall-size) * 1.2);  
            font-size: calc(var(--base-size) * 1.2);
        }

        .size-xl {
            height: calc(var(--overall-size) * 1.4);
            line-height: calc(var(--overall-size) * 1.4);  
            width: calc(var(--overall-size) * 1.4);  
            font-size: calc(var(--base-size) * 1.4);
        }

        .size-xxl {
            height: calc(var(--overall-size) * 1.8);
            line-height: calc(var(--overall-size) * 1.8);  
            width: calc(var(--overall-size) * 1.8);  
            font-size: calc(var(--base-size) * 1.8);
        }

        div.labeled.size-sm {
            padding: 0.2rem 0.4rem;   
        }

        div.labeled.size-lg {
            padding: 0.24rem 0.48rem;   
        }

        div.labeled.size-xl {
            padding: 0.28rem 0.56rem;   
        }

        div.labeled.size-xxl {
            padding: 0.36rem 0.72rem;   
        }

        div.right-square {
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
        }
        div.left-square {
            border-top-right-radius: 0;
            border-bottom-right-radius: 0;
        }
        div:hover {
            background-color: rgba(var(--primary-color), 0.08);
            color: rgb(var(--primary-color));
        }
        div.toggle.active {
            color: rgb(var(--primary-color)); 
        }
        div:hover {
            background-color: rgba(var(--primary-color), 0.08);
        }

        /* Filled Icon Button */
        div.filled {
            background-color: rgb(var(--primary-color));
            color: rgb(var(--on-primary-color));
        }

        div.filled.toggle {
            background-color: rgb(var(--surface-variant-color));
            color: rgb(var(--primary-color));
        }
        div.filled.toggle.active {
            background-color: rgb(var(--primary-color));
            color: rgb(var(--on-primary-color));
        }
        div.filled.toggle::hover {
            background-color: rgb(var(--primary-color));
            color: rgb(var(--on-primary-color));
            opacity: 0.8;
        }

        /* Tonal Icon Button */
        div.tonal {
            background-color: rgb(var(--secondary-container-color));
            color: rgb(var(--on-secondary-containercolor));
        }

        div.tonal.toggle {
            background-color: rgb(var(--surface-variant-color));
            color: rgb(var(--on-surface-variant-color));
        }
        div.tonal.toggle.active {
            background-color: rgb(var(--secondary-container-color));
            color: rgb(var(--on-secondary-containercolor));
        }
        div.tonal.toggle::hover {
            background-color: rgb(var(--secondary-container-color));
            color: rgb(var(--on-secondary-containercolor));
            opacity: 0.8;
        }
    `;

    static properties = {
        icon: {
            type: String
        },
        label: {
            type: String
        },
        size: {
            type: String
        },
        toggle: {
            type: Boolean
        },
        active: {
            type: Boolean
        },
        filled: {
            type: Boolean
        },
        tonal: {
            type: Boolean
        },
        'full-square': {
            type: Boolean
        },
        'left-square': {
            type: Boolean
        },
        'right-square': {
            type: Boolean
        },
        disable: {
            type: Boolean
        }
    };

    constructor() { 
        super();
        this.addEventListener('click', this._clickHandler);
        this.size = 'md';
        this.family = 'outlined';
        this.toggle = false;
        this.active = false;
        this.filled = false;
        this.tonal = false;
        this.disable = false;

        this.label = false;

        this['full-square'] = false;
        this['left-square'] = false;
        this['right-square'] = false;
    }
    
    willUpdate(changedProperties) {
        // only need to check changed properties for an expensive computation.
        if (changedProperties.has('toggle') || changedProperties.has('active')) {
          if (this.toggle) {
            this.family = this.active? 'filled' : 'outlined';
          }
        }
    }
      
    render() {
        let classes = { 
            labeled: this.label,
            toggle: this.toggle, active: this.active,
            filled: this.filled, tonal: this.tonal,
            'size-sm': this.size == 'sm', 'size-md': this.size == 'md', 'size-lg': this.size == 'lg', 'size-xl': this.size == 'xl', 'size-xxl': this.size == 'xxl',
            'left-square': this['full-square'] || this['left-square'],
            'right-square': this['full-square'] || this['right-square']};
        let iconTpl = html`<zn-icon name="${this.icon}" .size="${this.size}" .family="${this.family}"><zn-icon>`;
        let labelTpl = html`<span>${this.label}<span>`;
        return html`
            <div class=${classMap(classes)}>
                ${ this.icon? iconTpl : null }
                ${ this.label? labelTpl : null }
            </div>
        `;
    }

    _clickHandler (e) {
        if (!this.disable) {
            hapticResponse();
            if (this.toggle) {
                const options = {
                    detail: !this.active,
                    bubbles: true,
                    composed: true,
                };
                this.dispatchEvent(new CustomEvent('toggle', options));
            }
        } else {
            e.stopImmediatePropagation();
        }
    }
}
customElements.define('zn-button', Button);