import {LitElement, html, css} from 'lit';
import {classMap} from 'lit/directives/class-map.js';
import {styleMap} from 'lit/directives/style-map.js';

export const requestTimeout = (fn, delay, registerCancel) => {
    const start = new Date().getTime();
  
    const loop = () => {
      const delta = new Date().getTime() - start;
  
      if (delta >= delay) {
        fn();
        registerCancel(noop);
        return;
      }
  
      const raf = requestAnimationFrame(loop);
      registerCancel(() => cancelAnimationFrame(raf));
    };
  
    const raf = requestAnimationFrame(loop);
    registerCancel(() => cancelAnimationFrame(raf));
};

const noop = () => {};


export class Clock extends LitElement {
    static styles = css`
        :host {
            display: inline-flex;
            position: relative;
            user-select:none;
            flex-wrap: wrap;
            width: 100%;
        }
        .clock-container {
            display: flex;
            align-items: center;
        }
        .clock-container div {
            background-color: rgba(var(--on-surface-color), 0.08);
            padding: var(--gutter-size);
            margin: 0 calc(var(--gutter-size)/2);
            border-radius: var(--gutter-size);
        }
        .clock-container.size-sm {
            font-size: calc(var(--base-size) * 0.8);
        }

        .clock-container.size-lg {
            font-size: calc(var(--base-size) * 1.2);
        }

        .clock-container.size-xl {
            font-size: calc(var(--base-size) * 1.4);
        }

        .clock-container.size-xxl {
            font-size: calc(var(--base-size) * 1.8);
        }
        .clock-container.size-sm div {
            margin: 0 calc((var(--gutter-size)/2) * 0.8);
            font-size: calc(var(--base-size) * 0.8);
        }

        .clock-container.size-lg div {
            margin: 0 calc((var(--gutter-size)/2) * 1.2);
            font-size: calc(var(--base-size) * 1.2);
        }

        .clock-container.size-xl div {
            margin: 0 calc((var(--gutter-size)/2) * 1.4); 
            font-size: calc(var(--base-size) * 1.4);
        }

        .clock-container.size-xxl div {
            margin: 0 calc((var(--gutter-size)/2) * 1.8);
            font-size: calc(var(--base-size) * 1.8);
        }
        .clock-container div:first-child {
            margin-left: 0;
        }

        .clock-container div:last-child {
            margin-right: 0;
        }

        .clock-container.size-custom,
        .clock-container.size-custom div {
            font-size: var(--custom-size);
        }
    `;

    static properties = {
        label: {
            type: String
        },
        icon: {
            type: String
        },
        size: {
            type: String
        },
        seconds: {
            type: Boolean
        },
        _displayHours: {
            state: true
        },
        _displayMinutes: {
            state: true
        },
        _displaySeconds: {
            state: true
        }

    };

    constructor() { 
        super();
        this.label = null;
        this.cancelTimeout = noop;
        this.icon = null;
        this.seconds = false;
        this.size = 'md';
        this._displayHours = '--';
        this._displayMinutes = '--';
        this._displaySeconds = '--';
    }
    connectedCallback() {
        super.connectedCallback();
        let self = this;
        requestTimeout(self._updateTime.bind(self), 1000,  fn => self.cancelTimeout = fn);
    }
    
    _updateTime () {
        const now = new Date();
        let self = this;
        this._displayHours = now.getHours().toString().padStart(2, '0');
        this._displayMinutes = now.getMinutes().toString().padStart(2, '0');
        if (this.seconds) {
            this._displaySeconds = now.getSeconds().toString().padStart(2, '0');
        }
        requestTimeout(self._updateTime.bind(self), 1000,  fn => self.cancelTimeout = fn);
    }
    render() {
        let classes = {
            'clock-container': true,
            'size-sm': this.size == 'sm', 'size-md': this.size == 'md', 'size-lg': this.size == 'lg', 'size-xl': this.size == 'xl', 'size-xxl': this.size == 'xxl',
            'size-custom': this.size !== 'sm' && this.size !== 'md' && this.size !== 'lg' && this.size !== 'xl' && this.size !== 'xxl'
        };
        let styles = {
            '--custom-size': this.size !== 'sm' && this.size !== 'md' && this.size !== 'lg' && this.size !== 'xl' && this.size !== 'xxl'? this.size : undefined
        };
        let clock = this.seconds
        ? html`<div>${this._displayHours}</div>:<div>${this._displayMinutes}</div>:<div>${this._displaySeconds}</div>`
        : html`<div>${this._displayHours}</div>:<div>${this._displayMinutes}</div>`;
        return html`
            <div class=${classMap(classes)} style=${styleMap(styles)}>
                ${clock}
            </div>`;
    }
    disconnectedCallback() {
        super.disconnectedCallback()
        this.cancelTimeout();
    }
}
customElements.define('zn-clock', Clock);