import {LitElement, html, css} from 'lit';
import './components/common/layout/App.js';
import './components/common/layout/Page.js';
import './components/common/layout/StackContainer.js';
import './components/common/container/Card.js';
import './components/common/content/ContentItem.js';
import './components/common/button/Button.js';
import './components/player/SelectorCard.js';
import './components/player/ClockCard.js';

import {channel} from './helper/soundChannel.js'

import './style.css'

import {background, voice} from './data/sounds.js'

let backgroundChannel = channel('background', background);
let voiceChannel = channel('voice', voice);

export class SleepBaby extends LitElement {
  static styles = css`
  :host {
    width: 100vw;
    height: 100vh;
    display: block;
  }
  .test-block {
    min-height: 2em;
    background-color: salmon;
  }

  .test-block {
    min-height: 2em;
    background-color: salmon;
  }
  .test-block:nth-child(even){background-color: #f2f2f2;}

`;
  static properties = {
    _darkMode: {
      state: true
    }
  };

  constructor() {
    super();
    this._darkMode = false;

  }

  _handlePageAction (e) {
    console.log(e)
    e.stopImmediatePropagation();

  }
  _toggleDarkMode () {
    this._darkMode = !this._darkMode;
  }

  _togglePlay () {
    if (backgroundChannel.isPlaying()) {
      backgroundChannel.pause();
    } else {
      backgroundChannel.play();
    }
    if (voiceChannel.isPlaying()) {
      voiceChannel.pause();
    } else {
      voiceChannel.play();
    }
  }


  render() {
    const homeActions = [{ id: 'help', icon: 'help', label: 'About'}]
    return html`
    <zn-app ?dark=${this._darkMode}>
      <zn-page title="Sleep Baby" .actions="${homeActions}" @action="${this._handlePageAction}">
        <zn-stack-container columns="1">
          <clock-card ?dark-mode=${this._darkMode} @toggle="${this._toggleDarkMode}"></clock-card>
          <selector-card
            channel="background"
            title="Melodia"
            label="Selecciona una melodia"
            image="assets/img/sleepbaby.png">
          </selector-card>
          <selector-card
            use-labels
            channel="voice"
            title="Voz"
            icon="record_voice_over"
            label="Selecciona un seseo como complemento"
            image="assets/img/silence.png">
          </selector-card>
          <zn-card type="filled">
            <zn-content-item>
              <zn-button icon="power" @click="${this._togglePlay}"></zn-button>
            </zn-content-item>
          </zn-card>
          </zn-card>
        </zn-stack-container>
      </zn-page>
    </zn-app>
    `;
  }
}
customElements.define('sleep-baby', SleepBaby);

if ("serviceWorker" in navigator){
  window.addEventListener("load", () => {
    navigator.serviceWorker.register("/sw.js");
  })
}