import {LitElement, html, css} from 'lit';
import '../common/container/Card.js';
import '../common/content/ContentItem.js';
import '../common/content/Image.js';
import '../common/content/Icon.js';
import '../common/button/ButtonBar.js';
import { channel } from '../../helper/soundChannel.js';

const defaults = {
    thumb: 'assets/img/doom.jpg',
    label: 'Select a sound to include'
}
export class SelectorCard extends LitElement {
    static styles = css`
        :host {
            display:inline-flex;
            vertical-align:middle;
            user-select:none;
        }
        .content-top {
            display: flex;
            width: 100%;
            justify-content: space-between;
            padding: 0 var(--gutter-size);
        }
        zn-button-bar {
            align-self: flex-start;
        }

        .current-label {
            width: 100%;
            padding: 0 var(--gutter-size);
        }
    `;

    static properties = {
        channel: {
            type: String
        },
        title: {
            type: String
        },
        icon: {
            type: String
        },
        image: {
            type: String
        },
        label: {
            type: String
        },
        busy: {
            type: Boolean
        },
        'use-labels': {
            type: Boolean
        },
        _currentThumb: {
            sate: true
        },
        _currentLabel: {
            state: true
        },
        _options: {
            state: true
        },
        _current: {
            state: true
        }
    };

    constructor() { 
        super();
        this.channel = '';
        this.title = '';
        this.image = '';
        this.label = '';
        this.icon = 'music_note';
        this._current = null;
        this.busy = false;
        this['use-labels'] = false;
        this._currentThumb = this.image;
        this._currentLabel = this.label;
        this._options = [];

    }

    willUpdate(changedProperties) {
        // only need to check changed properties for an expensive computation.
        if (changedProperties.has('channel')) {
            //Forst get the options
            this._options = Object.values(channel(this.channel).sounds);
            //Assign selected value
            this._current = null;
        }
        if (changedProperties.has('_current')) {
            let ch = channel(this.channel);
            if (this._current == null || this._current == undefined) {
                ch.switch('');
                this._currentThumb = this.image;
                this._currentLabel = this.label;                     
            } else {
                ch.switch(this._current);
                this._currentThumb = ch.sounds[this._current].image;
                this._currentLabel = ch.sounds[this._current].songName;             
            }
        }
    }

    render() {
        return html`
            <zn-card type="filled" full-width="100%"> 
            <zn-content-item>
                <zn-image slot="left" width="5em" source="${this._currentThumb}" cover rounded></zn-image>
                <div class="content-top">
                    <div class="selector-title"><zn-icon .name="${this.icon}"></zn-icon>${this.title}</div>
                    <zn-button-bar filled size="lg" toggle disable-labels ?enable-labels="${this['use-labels']}" .value="${this._current}" .items="${this._options}" @selected="${this._handleSelection}"></zn-button-bar>
                </div>
                <div class="current-label">${this._currentLabel}</div>
            </zn-content-item>
            </zn-card>
        `;
      }
      _handleSelection (e) {
        let value = e.detail;
        //Intercept current value to disable if is the previous selected
        if (value == this._current) {
            value = null;
        }
        this._current = value;
    }
}
customElements.define('selector-card', SelectorCard);