import {LitElement, html, css} from 'lit';
import {repeat} from 'lit/directives/repeat.js';
import './Button.js';
import '../container/Menu.js';
import '../container/Popover.js';


export class ButtonBar extends LitElement {
    static styles = css`
        :host {
            display:inline-flex;
            vertical-align:middle;
            user-select:none;
        }
        zn-button {
            margin-right: var(--gutter-size);
        }
        zn-button::first-child {
            margin-left: var(--gutter-size);
        }
        zn-button[left-square] {
            margin-right: 0;
        }
        zn-button[full-square] {
            margin-right: 0;
        }
    `;

    static properties = {
        items: {
            type: String
        },
        toggle: {
            type: Boolean
        },
        value: {},
        size: {
            type: String
        },
        filled: {
            type: Boolean
        },
        tonal: {
            type: Boolean
        },
        "enable-labels": {
            type: Boolean
        },
        "max-actions": {
            type: Number
        },
        "more-icon": {
            type: String
        },
        _moreOpen: {
            sate: true
        }
    };

    constructor() { 
        super();
        this.items = [];
        this.toggle = false;
        this.value = null;

        this.size = 'md';
        this.filled = false;
        this.tonal = false;
        this["enable-labels"] = false;
        this["max-actions"] = 0;
        this["more-icon"] = 'more_vert';

        this._moreOpen = false;
    }
      
    render() {
        let lastIndex = this.items.length -1;
        if (this["max-actions"] == 0 || this["max-actions"] >= this.items.length) {
        return html`
            ${repeat(this.items, (item) => item.id, (item, index) => html`
              <zn-button .label="${this['enable-labels']? item.label : undefined}" .icon="${item.icon}" ?filled=${this.filled} ?tonal=${this.tonal}
                .size="${this.size}"
                ?toggle=${this.toggle}
                ?active=${this.value == item.id}
                ?full-square=${this.toggle && index > 0 && index < lastIndex}
                ?left-square=${this.toggle && index == 0}
                ?right-square=${this.toggle && index == lastIndex}
              
                @click="${(e) => { this._handleClick(item.id); e.stopImmediatePropagation();  return false;}}"></zn-button>`)}
        `;
        } else {
            return html`
                <zn-popover position="bottom-left" ?open=${this._moreOpen}>
                    <zn-button .icon="${this["more-icon"]}"
                        .size="${this.size}"
                        @click="${(e)=>{ this._moreOpen = !this._moreOpen;  e.stopImmediatePropagation();}}"></zn-button>
                    <zn-menu slot="content" .items="${this.items}" @click="${(e) => { this._moreOpen = false; }}"></zn-menu>
                </zn-popover>
            `;               
        }
      }
    _handleClick (id) {
        const options = {
            detail: id,
            bubbles: true,
            composed: true,
        };
        if (this.toggle) {
            this.dispatchEvent(new CustomEvent('selected', options));
            this.dispatchEvent(new CustomEvent('input', options));
        }
        this.dispatchEvent(new CustomEvent('click', options));        
    }
}
customElements.define('zn-button-bar', ButtonBar);