import {LitElement, html, css} from 'lit';
import {classMap} from 'lit/directives/class-map.js';
import {repeat} from 'lit/directives/repeat.js';
import '../content/Icon.js'

export class ContentItem extends LitElement {
    static styles = css`
        :host {
            display: inline-flex;
            position: relative;
            user-select:none;
            flex-wrap: wrap;
            width: 100%;
        }

        :host(:not(:first-child)) {
            margin-top: var(--gutter-size);
        }
        .item-container {
            width: 100%;
            height: 100%;
            display: flex;
            flex-wrap: nowrap;
            padding: 0 var(--gutter-size);
        }
        .item-container.big-size {
            font-size: calc(var(--base-size) * 1.2);
        }
        .center-side {
            flex-grow: 1;
            display: flex;
            flex-wrap: wrap
        }
        .content-label,
        .content-caption {
            flex-basis: 100%;
        }

        .center-side .content-caption {
            color: rgba(var(--on-surface-color), 0.8);
        }
    `;

    static properties = {
        clickeable: {
            type: Boolean
        },
        label: {
            type: String
        },
        caption: {
            type: String
        },
        icon: {
            type: String
        },
        big: {
            type: Boolean
        }

    };

    constructor() { 
        super();
        this.clickeable = false;
        this.label = null;
        this.caption = null;
        this.icon = null;
        this.big = false;
    }
      
    render() {
        let classes = {
            'item-container': true,
            'big-size': this.big
        };
        return html`
            <div class=${classMap(classes)} @click="${this._handleClick}">
                <div class="left-side">
                    <slot name="left">
                        <zn-icon .name="${this.icon}"></zn-icon>
                    </slot>
                </div>
                <div class="center-side">
                    <slot>
                        <div class="content-label">${this.label}</div>
                        <div class="content-caption">${this.caption}</div>
                    </slot>
                </div>
                <div class="right-side">
                    <slot name="right">
                    </slot>
                </div>
            </di>`;
    }
    _handleClick (e) {
        if (this.clickeable) {
            e.stopImmediatePropagation();
            const options = {
                bubbles: true,
                composed: true,
            };
            this.dispatchEvent(new CustomEvent('click', options));
        }    
    }
}
customElements.define('zn-content-item', ContentItem);