import {defer} from './utils.js'
const _channels = {};

export const channel = function (channelName, songs) {
  if (!_channels[channelName]) {
    console.log(`Sound: Create Channel ${channelName}`);
    _channels[channelName] = buildChannel(songs);
  }
  return _channels[channelName];
}

const buildChannel = function (songs) {
  let channel = {
    current: '',
    ready: defer(),
    shouldPlay: false,
    sounds: {},
    audio: new Audio()
  }
  if (songs && songs.length) {
    songs.forEach(element => {
      channel.sounds[element.id] = Object.assign({}, element);
    });
  }
  channel.ready.resolve(true);

  channel.switch = function (key) {
    channel.ready = defer();
    let playing = channel.shouldPlay;
    channel.audio.pause();
    channel.current = key;

    if (key !== '' && channel.sounds[key]) {
      channel.loopStart = channel.sounds[key].loopStart;
      channel.loopEnd = channel.sounds[key].loopEnd;

      channel.audio.loop = !(channel.loopStart && channel.loopEnd);
      channel.audio.addEventListener("loadeddata", () => {
        if (playing) {
          channel.play();
        } else {
          channel.ready.resolve(true)
        }
      }, { once: true });
      channel.audio.src = channel.sounds[key].url;
    } else {
      channel.ready.resolve("Channel Stopped")
    }
  }

  channel.play = function () {
    if (channel.current != '') {
      channel.ready = channel.audio.play();
      channel.shouldPlay = true;
    }
  }

  channel.pause = function () {
    channel.audio.pause();
    channel.shouldPlay = false;
    channel.audio.currentTime = 0;

  }
  channel.isPlaying = function () {
    return channel.shouldPlay;
  }

  //Event to control looping
  channel.audio.addEventListener('timeupdate', function () {
    if (!channel.audio.loop) {
      if (channel.audio.currentTime >= channel.loopEnd) {
        channel.audio.currentTime = channel.loopStart;
      }
    }
  });
  channel.audio.crossorigin="anonymous"
  return channel;
}