import {LitElement, html, css} from 'lit';
import {styleMap} from 'lit/directives/style-map.js';

export class ZnStackContainer extends LitElement {
  static styles = css`
  :host {
    width: 100%;
    height: 100%;
    display: block;
    overflow: hidden;
  }
  .stack-container {
    display: flex;
    height: 100%;
    flex-grow: 1;
    flex-wrap: wrap;
    align-content: flex-start;
    padding: calc(var(--gutter-size)/2);
  }
  ::slotted(*) {
    flex-basis: calc(100% / var(--columns) - var(--gutter-size));
    margin: calc(var(--gutter-size)/2);
  }
`;
  static properties = {
    columns: {
      type: String
    }
  };

  constructor() {
    super();
    this.columns = '1';
  }

  render() {
    const containerStyles = {
      '--columns': this.columns
    }
    return html`
      <div class="stack-container" style="${styleMap(containerStyles)}">
        <slot></slot>
      </div>
    `;
  }

}
customElements.define('zn-stack-container', ZnStackContainer);