import {LitElement, html, css} from 'lit';
import {classMap} from 'lit/directives/class-map.js';


export class Icon extends LitElement {
    static styles = css`
        :host {
            display: inline-flex;
            vertical-align: middle;
            user-select: none;
        }

        span {
            font-weight: normal;
            font-style: normal;
            font-size: var(--base-size);
            line-height: 1;
            letter-spacing: normal;
            text-transform: none;
            display: inline-block;
            width:100%;
            height:100%;
            white-space: nowrap;
            word-wrap: normal;
            direction: ltr;
            -webkit-font-feature-settings: 'liga';
            -webkit-font-smoothing: antialiased;
        }

        span.filled {
            font-family: 'Material Icons';
        }

        span.outlined {
            font-family: 'Material Icons Outlined';
        }

        span.round {
            font-family: 'Material Icons Round';
        }

        span.sharp {
            font-family: 'Material Icons Sharp';
        }

        span.two-tone {
            font-family: 'Material Icons Two Tone';
        }

        .size-md {
            font-size: var(--base-size);
        }

        .size-sm {
            font-size: calc(var(--base-size) * 0.6);
        }

        .size-lg {
            font-size: calc(var(--base-size) * 1.2);
        }

        .size-xl {
            font-size: calc(var(--base-size) * 1.4);
        }

        .size-xxl {
            font-size: calc(var(--base-size) * 1.8);
        }
    `;

    static properties = {
        name: {
            type: String
        },
        family: {
            type: String
        },
        filled: {
            type: Boolean
        },
        outlined: {
            type: Boolean
        },
        round: {
            type: Boolean
        },
        sharp: {
            type: Boolean
        },
        'two-tone': {
            type: Boolean
        },
        size: {
            type: String
        }
    };

    constructor() { 
        super();
        this.name = 'face';
        this.size = 'md';
        this.family = 'filled';
    }

    render() {
        let sizeClass = `size-${this.size}`;
        let classes = {};

        classes[sizeClass] = this.size !== '';
        classes[this.family] = true;

        return html`
            <span class=${classMap(classes)}>${ this.name }</span>
        `;
    }
}
customElements.define('zn-icon', Icon);